html{
  box-sizing: border-box;
}

*,
*::after,
*::before{
  box-sizing: inherit;
}

@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 300;
  src: url('../fonts/OpenSans-Light.ttf');
}

@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/OpenSans-Regular.ttf');
}

@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  src: url('../fonts/OpenSans-SemiBold.ttf');
}

a{
  text-decoration: none;
}

.container{
  max-width: 1220px;
  height: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

body{
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.025em;
  background: #ffffff;
}

@media (max-width: 600px) {
  body{
    font-size: 14px;
    line-height: 20px;
  }
}

.header{
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 3;
  box-shadow: 0 7px 20px -10px rgba(0, 0, 0, .5);
  font-weight: 400;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    z-index: 2;
  }
}

.header__inner{
  position: relative;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo{
  width: 80px;
  height: 80px;
  z-index: 3;
}

.header__burger{
  display: none;
}

.header__menu-list{
  height: 100px;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 2;
}

.header__menu-list-link{
  color: #666666;
  background: #ffffff;
  padding: 10px 18px 10px 18px;
  &:hover{
    color: #337ab7;
    background: #eeeeee;
  }
}

.header__menu-list-link-statement{
  padding: 10px 18px 10px 18px;
  border-radius: 20px;
  color: #ffffff;
  background: #337ab7;
}

@media (max-width: 890px) {
  body.lock{
    overflow: hidden;
  }
  .header__inner{
    height: 70px;
  }
  .header__logo{
    width: 60px;
    height: 60px;
  }
  .header__burger{
    display: block;
    position: relative;
    width: 42px;
    height: 32px;
    background: #337ab7;
    border-radius: 3px;
    z-index: 3;
    span{
      position: absolute;
      background-color: #ffffff;
      left: 0;
      width: 22px;
      height: 2px;
      top: 15px;
      cursor: pointer;
      transform: translateX(10px);
    }
    &:before, &:after{
      content: '';
      background-color: #ffffff;
      position: absolute;
      width: 22px;
      height: 2px;
      left: 0;
      transform: translateX(10px);
    }
    &:before{
      top: 9px;
    }
    &:after{
      bottom: 9px;
    }
  }

  .header__menu{
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 350px;
    overflow: auto;
    background-color: #ffffff;
    padding: 80px 0 20px 15px;
    transition: all 0.3s ease 0s;
    box-shadow: 0 7px 20px -10px rgba(0, 0, 0, .5);
    &.active{
      top: 0;
    }
  }
  .header__menu-list{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .header__menu-list-item{
    margin: 0 0 20px 0;
  }
}

