.input{
  margin-top: 8px;
  position: relative;
  color: #6f666a;
  width: 100%;
  label::after{
    position: absolute;
    content: "";
    background-image: url('../images/icons/required.png');
    background-size: 7px 7px;
    width: 7px;
    height: 7px;
    transform: translateY(100%) ;
    margin-left: 3px;
  }
  input{
    width: 100%;
    height: 52px;
    border: 1px solid #bababa;
    border-radius: 5px;
    margin-top: 4px;
    padding: 18px 15px 15px 15px;
    &::placeholder{
      color: #99999c;
    }
    &:focus::placeholder {
      opacity: 0;
    }
    &:focus{
      border: 1px solid #66afe9;
      box-shadow: 0 0 7px #cfe3f3;
      outline: 0;
    }
  }
}

.input__privacy-policy{
  margin-top: 23px;
  position: relative;
  color: #7c8593;
  width: 100%;
  display: flex;
  label::after{
    position: absolute;
    content: "";
    background-image: url('../images/icons/required.png');
    background-size: 7px 7px;
    width: 7px;
    height: 7px;
    transform: translateY(100%) ;
    margin-left: 3px;
  }
  a{
    color: #5572ac;
  }
}

.button__inner{
  margin: 35px 0 30px 0;
  button{
    display: block;
    margin: 0 auto;
    width: 368px;
    height: 51px;
    background: #337ab7;
    border: 1px solid #2e6da4;
    border-radius: 25px;
    font-size: 16px;
    color: #ffffff;
    text-transform: uppercase;
  }
}

